import axios from 'axios';
import React, { Component } from 'react';
import { withRouter } from 'react-router';





class SortFilter extends Component {

    constructor(props)
    {
        super(props);
    }

    renderdata = (event) =>{

        
        let sortv = event.target.value;
        
        console.log(sortv);
        let Url;

        

        var mealId = this.props.match.params.topic;


        let costUrl = `https://edurestapi.herokuapp.com/rest?mealtype=${mealId}`;
        

        if(sortv=="1" || sortv=="-1"){
            console.log('>>>','in sort');
            Url=`${costUrl}&sort=${sortv}`;
        }else{
            Url=`${costUrl}`;
        }


        

        

        axios.get(Url)
            .then((response) => {this.props.restPerSort(response.data)})

    }
    
    
    render() {
        return (
            <div>
                
                <br/>
                <p className="p">Sort</p>

                <div style={{padding:'20px',paddingTop:'0px',paddingBottom:'0px'}} onChange={this.renderdata}>

                    <label className="radio">    
                        <input type="radio" value="-1"  name="cost"/>&nbsp;<span> High to Low </span><br/>
                    </label>
                    <label className="radio">
                        <input type="radio" value="1"  name="cost"/>&nbsp;<span> Low to High</span><br/>
                    </label>
                    

                </div>

            </div>
        )
    }


}

export default withRouter(SortFilter) 
