import axios from 'axios';
import React, { Component } from 'react';
import { withRouter } from 'react-router';





class CuisineFilter extends Component {

    constructor(props)
    {
        super(props);
    }

    renderdata = (event) =>{

        
        let cuisineId = event.target.value;
        var mealid = this.props.match.params.topic;

        let rest = `https://edurestapi.herokuapp.com/rest?mealtype=${mealid}`;

        let cuisineUrl;
        if(cuisineId==''){
            cuisineUrl=`${rest}`
        }else{
            cuisineUrl=`${rest}&cuisine=${cuisineId}`
        }

        console.log(cuisineUrl);

        axios.get(cuisineUrl)
            .then((response) => {
                console.log('>>>>>444',response.data);
                this.props.restPerCuisine(response.data)
            })
    }
    
    
    render() {
        return (
            <div>
                
                <br/>
                <p className="p">Cuisine</p>

                <div style={{padding:'20px',paddingTop:'0px',paddingBottom:'0px'}} onChange={this.renderdata}>

                    
                <label className="radio">
                        <input type="radio" value="" name="cost"/><span>All</span>
                    </label>
                    <label className="radio">
                        <input type="radio" value="1" name="cost"/><span>North Indian</span>
                    </label>
                    <label className="radio">
                        <input type="radio" value="2" name="cost"/><span>South Indian</span>
                    </label>
                    <label className="radio">
                        <input type="radio" value="3" name="cost"/><span>Chinese</span>
                    </label>
                    <label className="radio">
                        <input type="radio" value="4" name="cost"/><span>Fast Food</span>
                    </label>
                    <label className="radio">
                        <input type="radio" value="5" name="cost"/><span>Street Food</span>
                    </label>
                </div>

            </div>
        )
    }


}

export default withRouter(CuisineFilter) 
